#our-team-hero-container {
    margin-top: 0.4rem;
    height: 25vw;
    background-image: url(../../images/ClassPic8.webp);
    background-size: cover;
    background-position: 100%;
    display: flex;
    justify-content: right;
    padding-right: 4rem;
    color: white;
    text-shadow: black 2px 2px 6px;
    font-size: 10vw;
}


/* mobile portrait */
@media (max-width:500px) {
    #our-team-hero-container {
        height: 50vw;
        padding: unset;
        font-size: 7.3vw;
        align-items: center;
        justify-content: center;
    }
}